.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
/** Small **/
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

/** Medium **/
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

/** Large **/
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
}